import React, { Component } from 'react';
import './Header.css';
import searchicon from './searchicon.PNG';

class Search extends Component {
    render() {
        return (
            <div id='search' className='pr3 pointer'>
                <span><img src={searchicon} 
                alt='search'
                id='searchpic' /></span>
                
                
            </div>
        )
    }
}

export default Search;