import React, { Component } from 'react';
import usericon from './usericon.PNG';

class UserIcon extends Component {
        
  render() {
    return (
      <div className='pl3 pointer'
      id='userhold'>
      
         <img  src={usericon} 
         alt='message'
         id='userpic' />       
      </div>
    );
  }
}

export default UserIcon;
