import React, { Component } from 'react';
import './Events.css'
import calpic from './calpic.PNG';
import tourpic from './tourpic.PNG';
import refresh from './refresh-button.png';


class Events extends Component {
  render() {
    return (
      <div id='eventbox'>

        <h1 className='tc' id='eventtext'>What kind of event you want to create?</h1>
         <div className='boxes pointer grow shadow-4 boxe'>
          <img src={calpic} alt='calpic'
          className='calpic' />
            <h2 className='evento'>One Time</h2>
            <p className='tc eventop'>Schedule a One Time Event!</p>
         </div>

         <div className='boxes pointer grow shadow-4'id='recur'>
            <img src={refresh} alt='refresh' 
            className='refresh'/>
            <h2 className='eventip'>Recurring</h2>
            <p className='tc eventi'>Schedule a Recurring Event!</p>
         </div>

         <div className='boxes pointer grow shadow-4 boxe'>
          <img src={tourpic} alt='tourpic'
          className='tourpic' />
            <h2 className='evento'>Tour</h2>
            <p className='tc eventop'
            >Going on a Tour? Click Here!</p>
         </div>
          
      </div>
    );
  }
}

export default Events;
